.allTradesTable {
  max-height: 254px; }
  .allTradesTable th:nth-child(1), .allTradesTable td:nth-child(1) {
    width: 152px;
    flex-basis: 152px; }
  .allTradesTable th:nth-child(2), .allTradesTable td:nth-child(2) {
    width: 152px;
    flex-basis: 152px; }
  .allTradesTable th:nth-child(3), .allTradesTable td:nth-child(3) {
    width: 135px;
    flex-basis: 135px; }
  .allTradesTable td:last-child, .allTradesTable th:last-child {
    flex-grow: 1; }

.loader {
  display: inline-block;
  width: 16px;
  min-height: 16px;
  margin-right: 4px;
  background-size: contain;
  background-image: url("../../utils/loadingIndicator/LoadingIndicatorLight.gif");
  background-repeat: no-repeat;
  background-position: center;
  opacity: 0.5; }



