.panel {
  background-color: #2F2F38;
  border: 1px solid #2B2B33;
  box-shadow: 0 0 4px 1px rgba(0, 0, 0, 0.2);
  border-radius: 4px;
  display: flex;
  flex-direction: column; }

.panelWithFooterBordered {
  padding-bottom: 19px;
  position: relative; }
  .panelWithFooterBordered:after {
    content: '';
    position: absolute;
    bottom: 18px;
    width: 100%;
    height: 0;
    border-top: 1px solid #393942; }

.panelHeader {
  display: flex;
  align-items: center;
  padding: 12px 16px;
  text-transform: uppercase;
  font-weight: 500;
  letter-spacing: 0.36px;
  min-height: 64px; }

.panelHeaderBordered {
  padding-bottom: 11px;
  border-bottom: 1px solid #393942; }

.panelBodyHorizontal {
  padding-left: 16px;
  padding-right: 16px; }

.panelBodyBottom {
  padding-bottom: 16px; }

.panelBodyTop {
  padding-top: 16px; }

.panelBodyScrollable {
  overflow-x: auto; }

.panelFooter {
  padding: 10px 16px 16px; }

.panelFooterBordered {
  border-top: 1px solid #393942; }



