header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-top: 1em;
  margin-bottom: 0.75em; }
  header .logo {
    width: 132px;
    max-height: 37px;
    margin: auto 1.5em auto auto; }
    header .logo:hover :global #bg {
      fill-opacity: 0.35; }

section {
  display: inline-flex; }

nav ul {
  display: flex;
  list-style-type: none;
  padding: 0; }
  nav ul li + li {
    margin-left: -1px; }
  nav ul li:first-child > * {
    border-bottom-left-radius: 4px;
    border-top-left-radius: 4px; }
  nav ul li:last-child > * {
    border-bottom-right-radius: 4px;
    border-top-right-radius: 4px; }
  nav ul li:last-child {
    margin-right: 0; }

.navElement, .navLink {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  height: 40px;
  border: 1px solid rgba(255, 255, 255, 0.1);
  color: #fff;
  border-radius: 0; }

.navLink {
  text-decoration: none;
  width: 108px;
  color: #87878C; }
  .navLink:hover {
    background-color: rgba(255, 255, 255, 0.1);
    cursor: pointer; }

.activeNavLink, .activeNavLink:hover {
  color: white;
  border-color: #87878C;
  background: initial; }

.account {
  width: 196px;
  border-radius: 4px;
  margin-right: 12px; }

.networkIndicator {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  width: 79px;
  height: 40px;
  background: transparent;
  border-radius: 4px;
  font-family: 'Montserrat', sans-serif;
  font-weight: 600;
  font-size: 16px;
  letter-spacing: 1.4px;
  text-transform: capitalize; }

.networkIndicator.kovan {
  color: #7E57C2;
  border: 1px solid #7E57C2; }

.networkIndicator.main {
  color: #69F0AE;
  border: 1px solid #69F0AE; }



