.table th {
  text-transform: unset; }

.table .center {
  text-align: center; }

.table .amount {
  text-align: right; }

.table td:nth-child(2) {
  padding-left: 8px; }

.table td:nth-child(2),
.table td:nth-child(3) {
  padding-top: 2px;
  padding-bottom: 2px; }

.table td:nth-child(4) {
  padding-top: 0;
  padding-bottom: 0; }

.centeredAsset {
  display: inline-flex;
  align-items: center; }

.transferBtn {
  background-color: transparent;
  border: 1px solid #58585F;
  border-radius: 100%;
  display: inline-flex;
  justify-content: center;
  align-content: center;
  padding: 0;
  color: #fff;
  border-color: #58585F;
  background-color: #2F2F38; }
  .transferBtn:hover {
    cursor: pointer; }
  .transferBtn:hover[disabled], .transferBtn:hover.disabled {
    cursor: default; }
  .transferBtn:hover:enabled:not(.disabled), .transferBtn:focus:enabled:not(.disabled), .transferBtn:active:enabled:not(.disabled) {
    color: #fff;
    background-color: #64646c;
    border-color: #3b3b46; }
  .transferBtn[disabled]:hover, .transferBtn[disabled], .transferBtn.disabled:hover, .transferBtn.disabled {
    opacity: 0.5; }

.transferBtnLeft {
  transform: rotate(180deg);
  margin-right: 5px; }



