.modal {
  position: absolute;
  left: 20px;
  top: 20px;
  bottom: 20px;
  right: 20px;
  background: transparent;
  border: none;
  border-radius: 0;
  display: flex;
  padding: 5px;
  outline: none;
  overflow: auto; }
  .modal:after {
    content: '';
    display: block;
    height: 25px;
    width: 100%;
    position: fixed;
    bottom: 0;
    background: linear-gradient(to bottom, rgba(31, 31, 39, 0) 0%, #1f1f27 25%); }
  .modal:before {
    content: '';
    display: block;
    height: 25px;
    width: 100%;
    position: fixed;
    top: 0;
    background: linear-gradient(to top, rgba(31, 31, 39, 0) 0%, #1f1f27 25%); }

.modalChild {
  margin: auto; }

.modalOverlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(21, 21, 29, 0.8); }

.checklistLine {
  display: flex;
