.switchBtn {
  margin-right: 0;
  padding: 0;
  width: 30px;
  height: 30px;
  fill: white; }

.spreadRow {
  background-color: rgba(255, 255, 255, 0.02); }

.orderbookTable {
  max-height: 389px; }
  .orderbookTable th:nth-child(1), .orderbookTable td:nth-child(1) {
    width: 146px;
    flex-basis: 146px; }
  .orderbookTable th:nth-child(2), .orderbookTable td:nth-child(2) {
    width: 146px;
    flex-basis: 146px; }
  .orderbookTable th:nth-child(3), .orderbookTable td:nth-child(3) {
    width: 146px;
    flex-basis: 146px; }
  .orderbookTable td:last-child, .orderbookTable th:last-child {
    flex-grow: 1; }

:global(.order-enter) {
  opacity: 0.01; }

:global(.order-enter.order-enter-active) {
  opacity: 1;
  transition: opacity 1000ms ease-in; }

:global(.order-leave) {
  opacity: 1; }

:global(.order-leave.order-leave-active) {
  opacity: 0.01;
  transition: opacity 600ms ease-in; }



