.container {
  display: flex;
  align-items: center;
  width: 100%;
  padding: 0 30px 0 0; }

.dropdown {
  position: relative; }

.dropdownBtn {
  height: 48px;
  width: 220px;
  border-right: 1px solid rgba(255, 255, 255, 0.1);
  border-top-left-radius: 4px;
  border-bottom-left-radius: 4px;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  background: url("./icon_arrow_down.svg") no-repeat 186px center; }

.dropdownBtnDisabled {
  cursor: default;
  background-image: none; }

.dropdownBtnActive {
  box-shadow: inset 1px 1px 7px 2px rgba(0, 0, 0, 0.3); }

.dropdownList {
  position: absolute;
  z-index: 10;
  width: 100%;
  top: 48px;
  list-style: none;
  padding: 0;
  margin: 0;
  background-color: #2F2F38;
  border: 1px solid #2B2B33;
  border-top: none;
  box-shadow: 0 0 4px 1px rgba(0, 0, 0, 0.2);
  border-bottom-left-radius: 4px;
  border-bottom-right-radius: 4px; }

.dropdownItem {
  display: block; }

.dropdownItem:not(:last-child) {
  border-bottom: 1px solid #2B2B33; }

.dropdownItemLink {
  display: flex;
  color: white;
  text-decoration: none; }
  .dropdownItemLink:hover {
    background-color: #393942;
    cursor: pointer; }

.pairView {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 12px 26px 12px 12px;
  width: 100%; }

.pairViewCurrency {
  flex-basis: 50px;
  flex-grow: 1; }

.pairViewIcon {
  flex-basis: 45px;
  display: flex;
  align-items: center; }

.active {
  font-weight: bold; }

.passive {
  font-weight: normal; }

.pairInfo {
  padding: 0 16px;
  flex-grow: 1;
  display: flex;
  align-items: center; }

.pairInfoLabel {
  margin-left: 7px; }



